"use client";

import { useState } from "react";
import { useOrders, Order } from "@/hooks/useOrders";
import { OrderDashboard } from "@/components/orders/OrderDashboard";
import { OrderList } from "@/components/orders/OrderList";
import { OrderForm } from "@/components/orders/OrderForm";
import { ImportExport } from "@/components/orders/ImportExport";
import { Button } from "@/components/ui/button";
import { Alert, AlertDescription } from "@/components/ui/alert";

export default function Home() {
  const {
    orders,
    loading,
    error,
    addOrder,
    updateOrder,
    deleteOrder,
    exportOrders,
    importOrders,
    getStats,
    clearError,
  } = useOrders();

  const [showForm, setShowForm] = useState(false);
  const [editingOrder, setEditingOrder] = useState<Order | null>(null);

  const stats = getStats();

  const handleAddOrder = (orderData: Omit<Order, 'id' | 'total' | 'createdAt'>) => {
    const result = addOrder(orderData);
    if (result) {
      setShowForm(false);
    }
  };

  const handleUpdateOrder = (orderData: Omit<Order, 'id' | 'total' | 'createdAt'>) => {
    if (editingOrder) {
      const updatedOrder: Order = {
        ...editingOrder,
        ...orderData,
        total: orderData.quantity * orderData.price,
      };
      const success = updateOrder(updatedOrder);
      if (success) {
        setEditingOrder(null);
        setShowForm(false);
      }
    }
  };

  const handleEditOrder = (order: Order) => {
    setEditingOrder(order);
    setShowForm(true);
  };

  const handleDeleteOrder = (id: string) => {
    deleteOrder(id);
  };

  const handleCancelForm = () => {
    setShowForm(false);
    setEditingOrder(null);
  };

  const handleNewOrder = () => {
    setEditingOrder(null);
    setShowForm(true);
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-black mx-auto mb-4"></div>
          <p className="text-gray-600">Loading orders...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="container mx-auto px-4 py-8 max-w-7xl">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900 mb-2">
            Service Sales Management System
          </h1>
          <p className="text-gray-600">
            Track orders, manage customers, and monitor your sales performance
          </p>
        </div>

        {/* Error Alert */}
        {error && (
          <Alert className="mb-6 border-red-200 bg-red-50">
            <AlertDescription className="text-red-700 flex justify-between items-center">
              {error}
              <Button
                variant="ghost"
                size="sm"
                onClick={clearError}
                className="text-red-700 hover:text-red-800"
              >
                Dismiss
              </Button>
            </AlertDescription>
          </Alert>
        )}

        {/* Dashboard Stats */}
        <OrderDashboard
          totalOrders={stats.totalOrders}
          totalSales={stats.totalSales}
          totalRevenue={stats.totalRevenue}
        />

        {/* Action Buttons */}
        <div className="flex flex-col sm:flex-row gap-4 mb-6">
          <Button
            onClick={handleNewOrder}
            className="bg-black hover:bg-gray-800 text-white"
          >
            Add New Order
          </Button>
        </div>

        {/* Order Form */}
        {showForm && (
          <div className="mb-6">
            <OrderForm
              order={editingOrder}
              onSubmit={editingOrder ? handleUpdateOrder : handleAddOrder}
              onCancel={handleCancelForm}
              isEditing={!!editingOrder}
            />
          </div>
        )}

        {/* Import/Export */}
        <div className="mb-6">
          <ImportExport
            onExport={exportOrders}
            onImport={importOrders}
          />
        </div>

        {/* Orders List */}
        <OrderList
          orders={orders}
          onEdit={handleEditOrder}
          onDelete={handleDeleteOrder}
        />
      </div>
    </div>
  );
}
