"use client";

import { useState, useEffect } from 'react';

export interface Order {
  id: string;
  customerName: string;
  productName: string;
  quantity: number;
  price: number;
  total: number;
  createdAt: string;
}

const STORAGE_KEY = 'service-sales-orders';

export const useOrders = () => {
  const [orders, setOrders] = useState<Order[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // Load orders from localStorage on mount
  useEffect(() => {
    try {
      const storedOrders = localStorage.getItem(STORAGE_KEY);
      if (storedOrders) {
        const parsedOrders = JSON.parse(storedOrders);
        setOrders(parsedOrders);
      }
    } catch (err) {
      setError('Failed to load orders from storage');
      console.error('Error loading orders:', err);
    } finally {
      setLoading(false);
    }
  }, []);

  // Save orders to localStorage whenever orders change
  const saveToStorage = (ordersToSave: Order[]) => {
    try {
      localStorage.setItem(STORAGE_KEY, JSON.stringify(ordersToSave));
    } catch (err) {
      setError('Failed to save orders to storage');
      console.error('Error saving orders:', err);
    }
  };

  // Add new order
  const addOrder = (orderData: Omit<Order, 'id' | 'total' | 'createdAt'>) => {
    try {
      const newOrder: Order = {
        ...orderData,
        id: Date.now().toString() + Math.random().toString(36).substr(2, 9),
        total: orderData.quantity * orderData.price,
        createdAt: new Date().toISOString(),
      };

      const updatedOrders = [...orders, newOrder];
      setOrders(updatedOrders);
      saveToStorage(updatedOrders);
      setError(null);
      return newOrder;
    } catch (err) {
      setError('Failed to add order');
      console.error('Error adding order:', err);
      return null;
    }
  };

  // Update existing order
  const updateOrder = (updatedOrder: Order) => {
    try {
      const updatedOrders = orders.map(order =>
        order.id === updatedOrder.id
          ? { ...updatedOrder, total: updatedOrder.quantity * updatedOrder.price }
          : order
      );

      setOrders(updatedOrders);
      saveToStorage(updatedOrders);
      setError(null);
      return true;
    } catch (err) {
      setError('Failed to update order');
      console.error('Error updating order:', err);
      return false;
    }
  };

  // Delete order
  const deleteOrder = (id: string) => {
    try {
      const updatedOrders = orders.filter(order => order.id !== id);
      setOrders(updatedOrders);
      saveToStorage(updatedOrders);
      setError(null);
      return true;
    } catch (err) {
      setError('Failed to delete order');
      console.error('Error deleting order:', err);
      return false;
    }
  };

  // Export orders as JSON
  const exportOrders = () => {
    try {
      return JSON.stringify(orders, null, 2);
    } catch (err) {
      setError('Failed to export orders');
      console.error('Error exporting orders:', err);
      return null;
    }
  };

  // Import orders from JSON
  const importOrders = (jsonData: string) => {
    try {
      const importedOrders = JSON.parse(jsonData);
      
      // Validate the imported data structure
      if (!Array.isArray(importedOrders)) {
        throw new Error('Invalid data format: expected an array');
      }

      // Validate each order object
      const validatedOrders = importedOrders.map((order: any, index: number) => {
        if (!order.id || !order.customerName || !order.productName || 
            typeof order.quantity !== 'number' || typeof order.price !== 'number') {
          throw new Error(`Invalid order format at index ${index}`);
        }
        
        return {
          ...order,
          total: order.quantity * order.price, // Recalculate total
          createdAt: order.createdAt || new Date().toISOString(),
        };
      });

      setOrders(validatedOrders);
      saveToStorage(validatedOrders);
      setError(null);
      return true;
    } catch (err) {
      const errorMessage = err instanceof Error ? err.message : 'Failed to import orders';
      setError(errorMessage);
      console.error('Error importing orders:', err);
      return false;
    }
  };

  // Calculate statistics
  const getStats = () => {
    const totalOrders = orders.length;
    const totalSales = orders.reduce((sum, order) => sum + order.quantity, 0);
    const totalRevenue = orders.reduce((sum, order) => sum + order.total, 0);

    return {
      totalOrders,
      totalSales,
      totalRevenue,
    };
  };

  return {
    orders,
    loading,
    error,
    addOrder,
    updateOrder,
    deleteOrder,
    exportOrders,
    importOrders,
    getStats,
    clearError: () => setError(null),
  };
};
