"use client";

import { useRef, useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Alert, AlertDescription } from "@/components/ui/alert";

interface ImportExportProps {
  onExport: () => string | null;
  onImport: (jsonData: string) => boolean;
}

export const ImportExport = ({ onExport, onImport }: ImportExportProps) => {
  const fileInputRef = useRef<HTMLInputElement>(null);
  const [message, setMessage] = useState<{ type: 'success' | 'error'; text: string } | null>(null);
  const [isProcessing, setIsProcessing] = useState(false);

  const handleExport = () => {
    try {
      setIsProcessing(true);
      const jsonData = onExport();
      
      if (jsonData) {
        const blob = new Blob([jsonData], { type: 'application/json' });
        const url = URL.createObjectURL(blob);
        const link = document.createElement('a');
        link.href = url;
        link.download = `orders-export-${new Date().toISOString().split('T')[0]}.json`;
        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
        URL.revokeObjectURL(url);
        
        setMessage({ type: 'success', text: 'Orders exported successfully!' });
      } else {
        setMessage({ type: 'error', text: 'Failed to export orders. Please try again.' });
      }
    } catch (error) {
      setMessage({ type: 'error', text: 'An error occurred during export.' });
      console.error('Export error:', error);
    } finally {
      setIsProcessing(false);
      setTimeout(() => setMessage(null), 3000);
    }
  };

  const handleImport = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    if (file.type !== 'application/json' && !file.name.endsWith('.json')) {
      setMessage({ type: 'error', text: 'Please select a valid JSON file.' });
      setTimeout(() => setMessage(null), 3000);
      return;
    }

    setIsProcessing(true);
    const reader = new FileReader();
    
    reader.onload = (e) => {
      try {
        const jsonData = e.target?.result as string;
        const success = onImport(jsonData);
        
        if (success) {
          setMessage({ type: 'success', text: 'Orders imported successfully!' });
        } else {
          setMessage({ type: 'error', text: 'Failed to import orders. Please check the file format.' });
        }
      } catch (error) {
        setMessage({ type: 'error', text: 'Invalid JSON file format.' });
        console.error('Import error:', error);
      } finally {
        setIsProcessing(false);
        setTimeout(() => setMessage(null), 3000);
        // Reset file input
        if (fileInputRef.current) {
          fileInputRef.current.value = '';
        }
      }
    };

    reader.onerror = () => {
      setMessage({ type: 'error', text: 'Failed to read the file.' });
      setIsProcessing(false);
      setTimeout(() => setMessage(null), 3000);
    };

    reader.readAsText(file);
  };

  const triggerFileInput = () => {
    fileInputRef.current?.click();
  };

  return (
    <Card className="border-0 shadow-sm">
      <CardHeader>
        <CardTitle className="text-lg font-semibold text-gray-900">
          Import / Export Data
        </CardTitle>
      </CardHeader>
      <CardContent className="space-y-4">
        {message && (
          <Alert className={message.type === 'error' ? 'border-red-200 bg-red-50' : 'border-green-200 bg-green-50'}>
            <AlertDescription className={message.type === 'error' ? 'text-red-700' : 'text-green-700'}>
              {message.text}
            </AlertDescription>
          </Alert>
        )}

        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div className="space-y-2">
            <Label>Export Orders</Label>
            <p className="text-sm text-gray-600 mb-3">
              Download all orders as a JSON file for backup or sharing.
            </p>
            <Button
              onClick={handleExport}
              disabled={isProcessing}
              className="w-full bg-black hover:bg-gray-800 text-white"
            >
              {isProcessing ? 'Exporting...' : 'Export Data'}
            </Button>
          </div>

          <div className="space-y-2">
            <Label>Import Orders</Label>
            <p className="text-sm text-gray-600 mb-3">
              Upload a JSON file to import orders. This will replace existing data.
            </p>
            <Input
              ref={fileInputRef}
              type="file"
              accept=".json,application/json"
              onChange={handleImport}
              disabled={isProcessing}
              className="hidden"
            />
            <Button
              onClick={triggerFileInput}
              disabled={isProcessing}
              variant="outline"
              className="w-full"
            >
              {isProcessing ? 'Importing...' : 'Import Data'}
            </Button>
          </div>
        </div>

        <div className="mt-4 p-3 bg-gray-50 rounded-md">
          <p className="text-sm text-gray-600">
            <strong>Note:</strong> Importing data will replace all existing orders. 
            Make sure to export your current data first if you want to keep it.
          </p>
        </div>
      </CardContent>
    </Card>
  );
};
